body {
    font-family: Arial, sans-serif;
    margin: 0;
    /*background-color: rgb(138, 138, 204);*/

    padding: 0;
}

.signupcontainer {
    max-width: 400px;
    margin: 50px auto;
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.67);
}

h2 {
    text-align: center;
    color: #333;
}

label {
    display: block;
    margin: 10px 0 5px;
    color: #555;
}

input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
}

button {
    background-color: #4caf50;
    color: #fff;
    padding: 10px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    width: 50%;
}

button:hover {
    background-color: #45a049;
}

.newuser {
    background-color: blue;
    margin-top: 10px; /* Add some margin to separate it from the button */
}

.newuser:hover {
    background-color: rgb(17, 17, 195);
}
.heading{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bolder;
}
