
body{
    padding: 8% 0% 0% 2%;
}

.images{
    width: 100%;
    height: 400px;
    display: flex;
    border: 4px solid white;
    border-radius: 16px;
    overflow-x: auto;
    position: relative;
    
}
.images > img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    flex-shrink: inherit;
    border: 5px solid white;
    border-radius: 16px;
    position: sticky;
    left: 0;
    cursor: pointer;
}
.images > img:hover{
    height: 100%;

}
.main{
    white-space: nowrap;
    overflow-x: auto;
    overflow-y: hidden;

}
.child{
    width: 200px;
    height: 180px;
    margin: 10px;
    display: inline-block;
    border-radius: 6px;
}
.child:hover{
    width: 250px;
    height: 200px;
    transition: 0.8s  ease;
}

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 20px;
    outline: none;
  }
